.sidebar {
  width: 16.5%;
  padding: 10rem 0 0 4rem;
  padding-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 2rem;
  align-self: stretch;
}
.sidebar span {
  width: 90%;
  border-top: 1px solid #66c0f4;
  padding-top: 2rem;
  font-size: 1.8rem;
  color: #c6d4df;
}
.sidebar ul {
  list-style: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1.5rem;
}
.sidebar ul button {
  color: #66c0f4;
  font-size: 1.5rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.sidebar ul button:hover,
.sidebar ul .active {
  color: #c6d4df;
}

@media (max-width: 768px) {
  .sidebar {
    padding: 0;
    margin: 2rem auto 4rem;
    width: 100%;
  }
  .sidebar span {
    font-size: 1.5rem;
    text-align: center;
    width: 100%;
    border-top: none;
    padding-bottom: 1rem;
    border-bottom: 1px solid #66c0f4;
  }
  .sidebar ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-self: start;
  }
  .sidebar ul li {
    justify-self: center;
  }
  .sidebar ul button {
    text-align: center;
  }
}