.content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.product-detail-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 3rem;
  min-width: 75rem;
  width: 65%;
  background-color: #172431;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  padding-bottom: 2rem;
  margin: 0 auto;
}
.product-detail-container .product-detail-upper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 2rem;
  width: 100%;
}
.product-detail-container .product-detail-upper img {
  width: 70%;
  max-width: 80rem;
  min-height: 33.5rem;
}
.product-detail-container .product-detail-upper .product-detail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 2rem;
  margin-top: 1.5rem;
  width: 40%;
  height: 32rem;
  padding-right: 2rem;
  overflow: hidden;
}
.product-detail-container .product-detail-upper .product-detail h2 {
  color: #fff;
  font-size: 2.5rem;
  font-family: "Roboto";
  letter-spacing: 2px;
}
.product-detail-container .product-detail-upper .product-detail p {
  font-size: 1.8rem;
  color: #cbcbcb;
  line-height: 2.5rem;
  overflow: hidden;
  height: 20rem;
  letter-spacing: 0.5px;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
}
.product-detail-container .product-detail-lower {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 2rem;
  width: 100%;
  padding: 2rem;
}
.product-detail-container .product-tag-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
}
.product-detail-container .product-tag-container span {
  font-size: 2rem;
  color: #fff;
}
.product-detail-container .product-tag-container .product-tags {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  list-style: none;
  color: #fff;
  font-size: 1.5rem;
}
.product-detail-container .product-tag-container .product-tags li {
  font-size: 1.8rem;
  color: #cbcbcb;
}
.product-detail-container .product-detail-end {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  gap: 0;
  width: 100%;
  height: 7rem;
}
.product-detail-container .product-detail-end .price-container {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  gap: 0;
}
.product-detail-container .product-detail-end .price-container .price-container-left {
  background-color: #4c6b22;
  color: #beee11;
  padding: 2rem;
  font-size: 3.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.product-detail-container .product-detail-end .price-container .price-container-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background-color: #344454;
  padding: 2rem;
}
.product-detail-container .product-detail-end .price-container .price-container-right .price {
  color: #73888e;
  text-decoration: line-through;
  font-size: 2rem;
}
.product-detail-container .product-detail-end .price-container .price-container-right .discounted-price {
  color: #beee11;
  font-size: 2rem;
}
.product-detail-container .product-detail-end .price-container .price-container-right .hidden + .discounted-price {
  color: #fff;
  width: 15rem;
  text-align: center;
}
.product-detail-container .product-detail-end .add-to-cart {
  cursor: pointer;
  font-weight: 600;
  font-size: 2rem;
  padding: 2rem 4rem;
  margin-right: 5%;
  background-color: rgb(113, 156, 49);
  color: #fff;
  border: 1px solid #000;
  text-decoration: none;
  text-align: center;
  width: 20rem;
}
.product-detail-container .product-detail-end .add-to-cart:hover {
  background-color: #beee11;
  color: #4e4e4e;
}

.hidden {
  display: none;
}

@media (max-width: 768px) {
  .content {
    width: 90%;
  }
  .product-detail-container {
    min-width: auto;
    width: 100%;
    gap: 1rem;
    height: auto;
  }
  .product-detail-container .product-detail-upper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
  .product-detail-container .product-detail-upper img {
    padding: 1rem;
    width: 100%;
    min-height: auto;
  }
  .product-detail-container .product-detail-upper .product-detail {
    margin-top: 0;
    width: 100%;
    padding: 1rem;
    height: auto;
  }
  .product-detail-container .product-detail-upper .product-detail h2 {
    width: 95%;
    font-size: 2rem;
    align-self: center;
  }
  .product-detail-container .product-detail-upper .product-detail p {
    align-self: center;
    width: 95%;
    font-size: 1.6rem;
  }
  .product-detail-container .product-detail-lower {
    padding-top: 0;
  }
  .product-detail-container .product-tag-container {
    width: 100%;
    gap: 1rem;
  }
  .product-detail-container .product-tag-container .product-tags {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  .product-detail-container .product-detail-end {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    gap: 3rem;
  }
  .product-detail-container .product-detail-end .price-container {
    height: 5rem;
  }
  .product-detail-container .product-detail-end .price-container .price-container-left,
  .product-detail-container .product-detail-end .price-container .price-container-right {
    padding: 0.5rem;
    flex: 1;
    font-size: 2.5rem;
  }
  .product-detail-container .product-detail-end .price-container .price-container-right .price,
  .product-detail-container .product-detail-end .price-container .price-container-right .discounted-price {
    font-size: 1.8rem;
  }
  .product-detail-container .product-detail-end .add-to-cart {
    width: 100%;
    padding: 1.5rem;
  }
}